import * as THREE from "three";

const threejsLoadManager = new THREE.LoadingManager();
const threejsLoader = new THREE.TextureLoader(threejsLoadManager);

/*
 images have been converted to base64 encoded text strings, 
 this is to resolve CORS issue in loading a local image file because of file::/// protocol
 we used this online tool to do the converting: https://www.base64-image.de/
*/
const backgroundImages = [
  {
    name: "World Map",
    file: "./image/worldmap.jpg",
    //"http://52.73.210.69:80/images/9d24372e67aebe4db3df48f72c79a47aleWPD7dIo+"
    data: `data:image/jpeg;base64,`,
  },
  {
    name: "Spring",
    file: "./image/spring.jpg",
    //"http://52.73.210.69:80/images/9d24372e67aebe4db3df48f72c79a47aksZET-Qui+"
    data: `data:image/jpeg;base64,`,
  },
  {
    name: "Summer",
    file: "./image/summer.jpg",
    //"http://52.73.210.69:80/images/9d24372e67aebe4db3df48f72c79a47aQonIpfk-u+"
    data: `data:image/jpeg;base64,`,
  },
  {
    name: "Autumn",
    file: "./image/autumn.jpg",
    //"http://52.73.210.69:80/images/9d24372e67aebe4db3df48f72c79a47aQonIpfk-u+"
    data: `data:image/jpeg;base64,`,
  },
  {
    name: "Winter",
    file: "./image/winter.jpg",
    //"http://52.73.210.69:80/images/9d24372e67aebe4db3df48f72c79a47al0CApRD7i+"
    data: `data:image/jpeg;base64,`,
  },
];

async function init() {
  for (let i = 0; i < backgroundImages.length; i++) {
    const img = backgroundImages[i];
    img.material = new THREE.MeshBasicMaterial({
      map: threejsLoader.load(img.data),
    });
  }
}
init();

/**
 * load images as threejs' materials
 */
export function getBackgroundImages() {
  return backgroundImages;
}
